import express from 'express';
import React from 'react';
import { renderToString, renderToStaticMarkup } from 'react-dom/server';
import { Provider } from 'mobx-react';
import { match, RouterContext } from 'react-router';
import path from 'path';
import cookieParser from 'cookie-parser';
import bodyParser from 'body-parser';
import morgan from 'morgan';
import assets from './assets.json';
import routes from './routes';
import Html from './html';
import fetch from 'node-fetch';
import configureStore from './configureStore';
import { initState } from './mock';

global.fetch = fetch;
global.navigator = global.navigator || {};
global.navigator.userAgent = global.navigator.userAgent || 'all';

const stores = configureStore(initState);

const app = express();

app.printSuccess = (attr, msg) => ({attr, code: 1, msg: msg || '', success: true});
app.printJSONPSuccess = (callback, attr, msg) => ('callback(' + JSON.stringify({attr, code: 1, msg: msg || '', success: true}) + ')');
app.printFailure = msg => ({code: 0, msg, success: false});
app.use(express.static(path.resolve(__dirname, '../client')));
app.use(cookieParser());
app.use(bodyParser.urlencoded({ extended: true }));
app.use(bodyParser.json());
app.use(morgan());

app.get('*', (req, res) => {
  match({ routes, location: req.url }, (err, redirectLocation, props) => {
    if (err) {
      res.status(500).send(err.message);
    } else if (redirectLocation) {
      res.redirect(302, redirectLocation.pathname + redirectLocation.search);
    } else if (props) {
      const matchRoutes = props.routes.slice();
      stores.routing._updateLocation(props.location);
      const route = matchRoutes.find(item => !!item.title);
      const data = {
        title: route.title + '_ - TD Risk Cloud ',
        styles: {
          // vendor1: assets.vendor1.css,
          reactServerRender: assets.reactServerRender.css
        },
        scripts: {
          minifest: assets.minifest.js,
          vendor1: assets.vendor1.js,
          vendor2: assets.vendor2.js,
          reactServerRender: assets.reactServerRender.js
        },
        children: renderToString(
          <Provider {...stores}>
            <RouterContext { ...props } />
          </Provider>
        ),
        initState
      };
      res.status(200);
      const html = renderToStaticMarkup(<Html {...data} />);
      res.send('<!doctype html>' + html);
    } else {
      res.sendStatus(404);
    }
  });
});

app.listen(3000, () => {
  console.log('The server is running at http://localhost:3000/');
});



