import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import cn from 'classnames';
import { Link } from 'react-router';

@inject('routing', 'appStore')
@observer
export default class Menus extends Component {
  render() {
    const { location, history } = this.props.routing;
    const { menus } = this.props.appStore;
    let current;
    if (location && location.pathname) {
      current = location.pathname.split('/').slice(3, 4).join('/');
    }
    return (
      <div className="sidebar2-func">
        <ul>
          {
            menus.map((menu, i) => (
              <li
                key={i}
                className={cn({
                  current: menu.url.indexOf(current) >= 0,
                  'sidebar2-func-item': true
                })}
                >
                <Link
                  to={menu.url}
                  onClick={() => {
                    this.props.routing._updateLocation(menu.url);
                    history.push(menu.url);
                  }}>
                  {menu.displayName}
                </Link>
              </li>
            ))
          }
        </ul>
      </div>
    );
  }
}



