import App from '../app';
import { PolicyList, PolicyView, PolicyHistory, RuleGroup, PolicyTemplateList, TemplateGroup } from '../pages';

const routes = [{
  path: '/',
  key: 'linkanalysis',
  component: App,
  indexRoute: { onEnter: (nextState, replace) => replace('/ruleengine/policy/policylist/policyList.htm') },
  childRoutes: [{
    path: 'ruleengine/policy/policylist/policyList.htm',
    key: 'PolicyList',
    chunk: 'PolicyList',
    component: PolicyList,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policyconfig/policyView.htm',
    key: 'PolicyView',
    chunk: 'PolicyView',
    component: PolicyView,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policyconfig/policyHistory.htm',
    key: 'PolicyHistory',
    chunk: 'PolicyHistory',
    component: PolicyHistory,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/ruleGroup.htm',
    key: 'RuleGroup',
    chunk: 'RuleGroup',
    component: RuleGroup,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policytemplate/policyTemplateList.htm',
    key: 'PolicyTemplateList',
    chunk: 'PolicyTemplateList',
    component: PolicyTemplateList,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/templateGroup.htm',
    key: 'TemplateGroup',
    chunk: 'TemplateGroup',
    component: TemplateGroup,
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }]
}];

export default routes;



